.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
    background-color: gray;
    padding:10px;
}

.dialogsItems .dialog{
    padding:5px;

   
}


.dialog a .avatarPlaceholder{
    display: inline-block;
    width:30px;
    height:30px;
    text-align: center;
    line-height: 28px;
    font-size:20px;
    background-color: white;
    color:hotpink;
    border-radius: 30px;
    margin-right:10px;

}

.dialog a:hover .avatarPlaceholder{
   
    background-color: rgba(136, 18, 77, 0.404);
    color:rgb(255, 255, 255);

}

.dialog a.active .avatarPlaceholder{
   
    background-color: rgb(136, 18, 77);
    color:rgb(255, 255, 255);

}




.dialog a {
    background-color: rgba(255, 255, 255, 0.5);
    color:white;
    text-decoration: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 50px;
}
.dialog a:hover {
    color:hotpink;
    background-color: rgba(255, 105, 180, 0.336);
   
}
.dialog a.active {
    background-color: hotpink;
    color:white !important;
}


.messages {
    padding:10px;
    display: flex;
    flex-direction: column;
    background-color: rgba(255, 255, 255, 0.247);
    margin-left:10px;
}

.newMessage {
    padding:10px;
    
}

.newMessage textarea{
    padding:10px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.747);
    width: 70%;
}

.newMessage button{
    padding:10px;
    border-radius: 10px;
    color: rgb(136, 18, 77);  
    /* //rgba(255, 255, 255, 0.747); */
    background-color:hotpink;
    width: 35%;
    letter-spacing: 3px;
    border: none;
    outline: 2px solid rgb(136, 18, 77);   
}

.newMessage button:hover{
    color:white;
    outline: 2px solid white;  

}

.newMessage textarea:focus{
    outline: 2px solid hotpink;
}


.messages .message{
    margin:2px;
    margin-bottom:5px;
    padding:2px 10px;
    padding-bottom:3px;
    background-color: hotpink;
    color:white;
    border-radius:20px;
    width: fit-content;


}


.messages .message.rightMessage{
    color:white;
    align-self: flex-end;

}